.element-list {
  list-style: none;
  padding-left: 0px;
  overflow-x: hidden;
  overflow-y: scroll;
  border: 1px solid lightgray;
  font-size: 0.75em;
  max-height: calc(100% - 150px);
}

.element-list li {
  border-bottom: 1px solid gray;
}

.element-list li.even {
  background-color: rgb(231, 231, 231);
}
.element-list.night li.even {
  background-color: rgb(68, 68, 68);
}

.element-list li:hover {
  background-color: darkgray;
  color: rgb(0, 0, 0);
}
.element-list.night li:hover {
  background-color: darkgray;
  color: rgb(255, 255, 0);
}

.element-list li.active {
  background-color: rgb(75, 75, 75);
  color: aliceblue;
}

.element-list.night li.active {
  background-color: rgb(128, 128, 128);
  color: aliceblue;
}

.list-header.active {
  background-color: rgb(182, 182, 182);
}

.list-header:hover {
  background-color: darkgray;
  color: black;
}
